import React from "react";

const Book = (props) => {
  // console.log(props);
  const { img, title, author, ratings } = props;
  console.log(props);

  const clickHandler = (e) => {
    console.log(e);
    console.log(e.target);
    alert("hello world");
  };

  const complexExample = (author) => {
    console.log(author);
  };
  return (
    <article
      className='book'
      onMouseOver={() => {
        console.log(ratings);
      }}
    >
      <img src={img} alt='' />
      <h1 onClick={() => console.log(title)}> {title} </h1>
      <h4 onClick={() => console.log(author)}> {author} </h4>
      <p> {ratings}</p>
      <button type='button' onClick={clickHandler}>
        reference example
      </button>
      <br />
      <button onClick={() => complexExample(author)}>
        more complex example
      </button>
    </article>
  );
};
export default Book;
