// ************ Event Basics ************

import React from "react";
import ReactDom from "react-dom";

//Css
import "./index.css";

// Importing Named Export file
import { books } from "./books";
//import { data } from "./books";

// Importing Named Export from another folder, so the relative path will be:
import { greeting } from "./testing/testing";

// Importing Default Export
import Book from "./Book";

// Importing Default Export by using different Name
//import BugsBunny from "./Book";

//Refactoring variables into Object

function BookList() {
  console.log(greeting);
  return (
    <section className='booklist'>
      {books.map((book) => {
        return <Book key={book.id} {...book}></Book>;
        // For Default Export
        // return <BugsBunny key={book.id} {...book}></BugsBunny>;
      })}
    </section>
  );
}

ReactDom.render(<BookList />, document.getElementById("root"));

